<template>
  <div>
    <vs-button @click="popupActive=true" color="warning" type="filled" icon-pack="feather" icon="icon-filter" icon-after>{{itemSelected[label]}}</vs-button>
    <vs-popup class="" title="Pilih" :active.sync="popupActive">
      <vs-table search :data="users" id="selectIdPopup" class="vs-con-loading__container">

        <template slot-scope="{data}">
          <vs-tr :key="indextr" v-for="(tr, indextr) in data">

            <vs-td :data="tr.title">
              <vx-card
                @click="selected(tr)"
                :title="tr[label]"
                title-color="primary">
                <div v-for="(valueObject, keyObject, indexO) in tr" :key="indexO">
                  <span v-if="keyObject != label && keyObject != 'id' && !isBlacklist(keyObject)"> 
                    <div class="text-warning">{{keyObject}}</div>
                    <div class="ml-4">{{ valueObject }}</div>
                  </span>
                </div>
              </vx-card>
            </vs-td>
          </vs-tr>
        </template>
      </vs-table>
    </vs-popup>
  </div>
</template>

<script>
export default {
  props:
  {
    value:Object,
    url:String,
    label:{
      type:String,
      default:'label'
    },
    blacklist:{
      type:Array,
      default:function () {
        return []
      }
    }
  }
  ,
  data() {
    return {
      popupActive: false,
      users: [],
      itemSelected:this.value,
    }
  },
  mounted(){
     this.$vs.loading({
        container: '#selectIdPopup',
        scale: 0.6
      })
    this.$http.get(this.url)
    .then((res)=>{
      this.users = res.data
      this.$vs.loading.close('#selectIdPopup > .con-vs-loading')
    })
    // .catch(function (error) {
    //   console.log(error.toJSON());
    //   this.$vs.loading.close('#selectIdPopup > .con-vs-loading')
    // });
  },
  methods:{
    selected(item){
      this.itemSelected = item
      this.$emit('input', item);
      this.popupActive= false
    },
    isBlacklist(str){
      return this.blacklist.includes(str)
    }
  }
  
}
</script>

